import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NavComponent } from './nav/nav.component';

const articleModule = () => import('../article/article.module').then(x => x.ArticleModule);
const employeeModule = () => import('../employee/employee.module').then(x => x.EmployeeModule);

const routes: Routes = [
    { path: '', component: HomeComponent },
    {
        path: 'profile', component: NavComponent, children: [
            { path: 'employee', loadChildren: employeeModule },
            { path: 'article', loadChildren: articleModule },
        ]

    },

    // otherwise redirect to home
    // { path: '**', redirectTo: '' }
];

@NgModule({
    declarations: [],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule]
})
export class LandingRoutingModule { }